var _this;
export default {
	data() {
		return {
			showPassword: false,
			captchaPath: '', //验证码
			captchaPathSh: true, //验证码

			showAgree: false, //协议是否选择
			isRotate: false, //是否加载旋转

			isRequest: false,
			ruleForm: {
				name:'',
				pass: '',
				checkPass: '',
				mobilePhone: '', //手机号码
				role: '-1', //角色
				captcha: '', //验证码
				uuid: '', //生成验证码对应的 id
			},
		}
	},

	mounted() {
		_this = this;
	},
	onReady() {
		_this.getVerCode();
		_this.isRequest = false; //后端没有返回验证码前 点击不了
	},
	methods: {
		display() {
			this.showPassword = !this.showPassword
		},
		isShowAgree() {
			//是否选择协议
			_this.showAgree = !_this.showAgree;
		},
		radioRole(res) {
			this.ruleForm.role = res.detail.value;
		},
		async getVerCode() {
			//注册
			if (this.isRequest) {
				uni.showModal({
					title: '提示',
					content: '上一个操作还没有响应，请稍后再试',
					success: function(res) {}
				});
				this.isRequest = false;
				return false;
			}
			this.isRequest = true;
			//获取验证码
			this.ruleForm.uuid = this.$http.getUuid();
			let uuidform = {
				uuid: this.ruleForm.uuid
			};
			let [err, res] = await this.$http.getV2(this.$urlconfig.logCaptcha, uuidform, {});
			this.isRequest = false;
			if (!this.$user.eck(err,true)) {
				this.captchaPathSh = false;
				return;
			}
			this.captchaPathSh = true;
			this.captchaPath = res.data.data
		},
		async submitForm() {
			//注册
			if (this.isRotate) {
				uni.showModal({
					title: '提示',
					content: '上一个操作还没有响应，请稍后再试',
					success: function(res) {}
				});
				this.isRequest = false;
				return false;
			}
			if (this.showAgree == false) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '请先同意《协议》'
				});
				return false;
			}
			if (this.ruleForm.name.length > 18 || this.ruleForm.name.length < 8) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '用户名在8-18位之间'
				});
				return false;
			}
			if (this.ruleForm.mobilePhone.length != 11) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '手机号不正确'
				});
				return false;
			}
			if (this.ruleForm.pass.length < 8 || this.ruleForm.pass.length > 18) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '密码要在8-18位'
				});
				return false;
			}

			if (this.ruleForm.checkPass.length < 8 || this.ruleForm.checkPass.length > 18) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '确认密码要在8-18位'
				});
				return false;
			}

			if (this.ruleForm.pass != this.ruleForm.checkPass) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '两次密码不一致'
				});
				return false;
			}
			if (this.ruleForm.captcha.length != 4) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '验证码不正确'
				});
				return false;
			}
			if (this.ruleForm.role == '-1') {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '请选择用户类型'
				});
				return false;
			}
			this.isRotate = true;
			let [err, res] = await this.$http.postV2(this.$urlconfig.logCreate, this.ruleForm, {});
			this.isRotate = false;
			if (!this.$user.eck(err)) {
				return;
			}
			if (!this.$user.checkLog(res, true)) {
				this.getVerCode();
				return;
			}
		}
	}
}
